.container {
    .main-logo {
        margin-top: 4em;
    }
}

@media (max-width:768px) {
    .container {
        .main-logo {
            margin-top: 0;
            img {
                transform: scale(0.6);
                transform-origin: left;
            }
        }
    }
}

